import { createSlice } from '@reduxjs/toolkit';

const listProductSLiceReducer = createSlice({
    name: 'listProduct',
    initialState: [
        {
            id: 1,
            name: 'Thnh ph H Ni',
            image: 'https://thanhit2612.github.io/luxstay/assets/imgs/location/location1.png',
            desc: 'Th  hn 1000 nm tui. Khng ch ni ting vi lch s lu i, giu bn sc, vn ha truyn thng dn tc, vng t ny cn c bit n l mt trong nhng a im thu ht nhiu du khch trong v ngoi nc',
        },
        {
            id: 2,
            name: 'Thnh ph  Lt',
            image: 'https://thanhit2612.github.io/luxstay/assets/imgs/location/location4.png',
            desc: 'c bit n l thnh ph sng m,  Lt thu ht bi kh hu mt m quanh nm, cnh quan thin nhin tuyt p. Gii thiu v  Lt gip du khch hiu thm v lch s, con ngi cng nh vn ha ca vng t ny.',
        },
        {
            id: 3,
            name: 'Thnh ph Vng Tu',
            image: 'https://thanhit2612.github.io/luxstay/assets/imgs/location/location3.png',
            desc: 'Vng Tu t lu  c xem l mt im n du lch hp dn, bi nhng v p t nhin ca mnh. D n bt c u trong chuyn du lch Vng Tu, du khch cng s b cun ht bi phong cnh ca ni y.',
        },
        {
            id: 4,
            name: 'Tnh Qung Ninh',
            image: 'https://thanhit2612.github.io/luxstay/assets/imgs/location/location7.png',
            desc: 'Qung Ninh l mt a danh giu tim nng du lch, l mt nh ca tam gic tng trng du lch min Bc Vit Nam. S hu danh thng ni ting l vnh H Long c UNESCO cng nhn l di sn thin nhin th gii v di sn th gii bi gi tr a cht a mo',
        },
        {
            id: 5,
            name: 'Thnh ph Hi An',
            image: 'https://thanhit2612.github.io/luxstay/assets/imgs/location/location8.png',
            desc: 'Khng ch c m mnh trong v p bnh d ca ph c, du lch Hi An bn cn c khm ph s giao thoa gia cc nn vn ha phng ng n t Nht Bn, Trung Quc v vn ha Vit. Bi Hi An vn l thng cng ng c tn ti di triu Nguyn cch y khong 200 nm.',
        },
    ],
    reducers: {
        addProduct: (state, action) => {
            state = state.push(action.payload);
        },
        deleteProduct: (state, action) => {
            const index = state.findIndex((x) => x.id === action.payload);
            if (index !== -1) state = state.splice(index, 1);
        },
    },
});

export default listProductSLiceReducer;

export const { addProduct, deleteProduct } = listProductSLiceReducer.actions;
